"use client";
import { motion } from "framer-motion";
import Image from "next/image";
import { Card, CardContent, CardHeader, CardTitle, CardFooter } from "../ui/card";
import { Button } from "../ui/button";
import { Badge } from "../ui/badge";
import { Github, ExternalLink } from "lucide-react";

// Example project data with ids and live URLs
const projects = [
  {
    id: 1,
    title: "Truth in Tales",
    description: "A web application that showcases different blogs to educate users about various topics.",
    tags: ["HTML", "CSS", "JavaScript"],
    githubUrl: "https://github.com/RandelYumul/Truth_in_Tales",
    liveUrl: "https://randelyumul.github.io/Truth_in_Tales/index.html",
  },
  {
    id: 2,
    title: "Capybara Game",
    description: "A fun 2D game where players guide a capybara to jump over obstacles while tracking scores and increasing difficulty.",
    tags: ["Python", "Pygame"],
    githubUrl: "https://github.com/yourusername/capybara-game",
    liveUrl: "#",
  },
  {
    id: 3,
    title: "VirtuoHero App",
    description: "A Flutter-based mobile app featuring a login page, user authentication, and smooth UI interactions.",
    tags: ["Flutter", "Dart"],
    githubUrl: "https://github.com/yourusername/virtuohero",
    liveUrl: "#",
  },
  {
    id: 4,
    title: "Personal Portfolio",
    description: "A modern, responsive portfolio website built to showcase projects, skills, and professional experience.",
    tags: ["React", "Next.js", "Tailwind CSS"],
    githubUrl: "https://github.com/yourusername/portfolio",
    liveUrl: "#",
  },
];

// Example project images array
const projectImages = [
  { id: 1, imageUrl: "/images/truth-in-tales.jpg", imageHint: "Blog web app screenshot" },
  { id: 2, imageUrl: "/images/capybara.png", imageHint: "Capybara game screenshot" },
  { id: 3, imageUrl: "/images/virtuohero.png", imageHint: "Flutter app screenshot" },
  { id: 4, imageUrl: "/images/portfolio.png", imageHint: "Portfolio website screenshot" },
];

export const Projects = () => {
  return (
    <motion.div
      id="projects"
      className="h-auto flex flex-col py-30"
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      viewport={{ once: true, amount: 0.3 }}
      transition={{ duration: 1 }}
    >
      <div className="flex flex-col items-center justify-start pt-0">
        <h1 className="text-5xl font-bold mb-8 text-center">Projects</h1>
        <p className="text-lg mb-8 max-w-xl text-center">
          A selection of my personal and collaborative projects demonstrating my technical skills and development experience.
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
        {projects.map((project, index) => {
          const image = projectImages.find(img => img.id === project.id);
          return (
            <Card
              key={project.id}
              className="flex flex-col overflow-hidden animate-in fade-in-0 zoom-in-95 bg-white dark:bg-black pt-0"
              style={{
                animationDelay: `${index * 150}ms`,
                animationFillMode: "both",
              }}
            >
              {image && (
                <div className="relative w-full aspect-video overflow-hidden">
                  <Image
                    src={image.imageUrl}
                    alt={project.title}
                    data-ai-hint={image.imageHint}
                    fill
                    className="object-cover w-full h-full transition-transform duration-300 hover:scale-105"
                  />
                </div>
              )}

              <CardHeader>
                <CardTitle>{project.title}</CardTitle>
              </CardHeader>

              <CardContent className="flex-1">
                <p className="text-muted-foreground mb-4">{project.description}</p>
                <div className="flex flex-wrap gap-2">
                  {project.tags.map(tag => (
                    <Badge key={tag} variant="secondary">{tag}</Badge>
                  ))}
                </div>
              </CardContent>

              <CardFooter className="flex justify-end gap-2">
                <Button variant="outline" size="sm" asChild>
                  <a href={project.githubUrl} target="_blank" rel="noopener noreferrer">
                    <Github className="mr-2 h-4 w-4" /> GitHub
                  </a>
                </Button>
                <Button size="sm" asChild>
                  <a href={project.liveUrl} target="_blank" rel="noopener noreferrer">
                    <ExternalLink className="mr-2 h-4 w-4" /> Live Demo
                  </a>
                </Button>
              </CardFooter>
            </Card>
          );
        })}
      </div>
    </motion.div>
  );
};
